n:column;gap:10px;margin-top:15px}.theme-button[data-v-13ae4df6]{width:100%;height:50px;border-radius:4px;cursor:pointer;border:2px solid var(--ui-border-primary);position:relative;display:flex;align-items:center;justify-content:center}.theme-button.light[data-v-13ae4df6]{background-color:#fafafa;color:#1a1a1a}.theme-button.dark[data-v-13ae4df6]{background-color:#2a2a2a;color:#e0e0e0}.theme-button.active[data-v-13ae4df6]{border-color:var(--ui-accent)}.theme-button[data-v-13ae4df6]{padding:5px}.theme-button span[data-v-13ae4df6]{position:absolute;bottom:5px;right:10px;font-size:0.8em;font-weight:bold}.setting-item[data-v-13ae4df6]{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px}h3[data-v-13ae4df6]{color:var(--ui-text-main);font-weight:500;font-size:1.2em}.wb-status-display[data-v-13ae4df6]{padding:15px;margin-bottom:20px;background:rgba(0,0,0,0.05);border-left:4px solid #999;position:relative;overflow:hidden}.wb-status-display .status-label[data-v-13ae4df6]{font-size:0.7em;letter-spacing:2px;color:var(--ui-text-secondary);margin-bottom:5px}.wb-status-display .status-value[data-v-13ae4df6]{font-size:1.2em;font-weight:bold;letter-spacing:1px}.wb-status-display .status-decor[data-v-13ae4df6]{position:absolute;top:0;right:0;width:20px;height:20px;background:linear-gradient(45deg,transparent 50%,rgba(255,255,255,0.1) 50%)}.status-green[data-v-13ae4df6]{border-left-color:#4caf50;color:#4caf50;background:rgba(76,175,80,0.05)}.status-blue[data-v-13ae4df6]{border-left-color:#2196f3;color:#2196f3;background:rgba(33,150,243,0.05)}.status-orange[data-v-13ae4df6]{border-left-color:#ff9800;color:#ff9800;background:rgba(255,152,0,0.05)}.wb-actions[data-v-13ae4df6]{display:flex;flex-direction:column;gap:15px;margin-bottom:20px}.ark-btn[data-v-13ae4df6]{position:relative;height:48px;display:flex;align-items:center;justify-content:center;cursor:pointer;border:1px solid var(--ui-border-primary);background:var(--ui-bg);transition:all 0.2s ease;overflow:h